
<template>
  <v-container fluid class="visual" v-bind:class="[$route.name]" ref="visual">
    <div name="block0" class="block block0 isLoad" v-bind:class="{ active: isLoad }">
      <router-view name="router0"></router-view>
      <guide axisV v-if="$route.name=='Landing'"></guide>
    </div>
    <div name="block1" class="block block1 isLoad" v-bind:class="{ active: isLoad }">
      <router-view name="router1"></router-view>
    </div>
    <div name="block2" class="block block2 isLoad" v-bind:class="{ active: isLoad }">
      <router-view name="router2"></router-view>
    </div>
    <div name="block3" class="block block3 isLoad" v-bind:class="{ active: isLoad }">
      <router-view name="router3"></router-view>
    </div>
    <div name="block4" class="block block4 isLoad" v-bind:class="{ active: isLoad }">
      <router-view name="router4"></router-view>
    </div>
    <div name="block5" class="block block5">
      <router-view name="router5"></router-view>
    </div>
    <div name="block6" class="block block6">
      <router-view name="router6"></router-view>
    </div>
    <div name="block7" class="block block7 isLoad" v-bind:class="{ active: isLoad }">
      <router-view name="router7"></router-view>
    </div>

    <div class="sub-title" v-bind:class="{ active: isLoad }">
      <h2 class="title-top headline" v-bind:data-title="isTitle">{{$store.state.isTitle}}</h2>
      <p class="title-des caption">{{$store.state.isTitleSub}}</p>
    </div>
  </v-container>
</template>

<!-- <i18n>
{
  "en": {
    "hello": "hello world!"
  },
  "ko": {
    "hello": "안녕하세요！"
  },
  "ja": {
    "hello": "こんにちは、世界！"
  }
}
</i18n> -->

<script>
import Velocity from "velocity-animate";
import store from "../store";
import router from "../router";
import Guide from "@/components/Guide.vue";

// const default_layout = 'default'
export default {
  components: {Guide},

  data() {
    return {
      isLoad: false,
      isEvent: false,
      activeClass: "cancel-transition",
      errorClass: "",
      hasError: false,
      isTitle: "title",
      istitleSub: "sub",
      appName: "HAEMILSOFT",
      sidebar: false,
      transitionName: "slide-top",
      locale: "en",
      colorItem: [
        "#1e9cd9",
        "#0277bd",
        "#2f5aa8",
        "#242424",
        "#313131"
      ],
      blocks: [
        {
          speed: [640, 375, 375, 375, 375, 375, 375, 375],
          width: [1500, 375, 375, 375, 375, 375, 375, 375],
          height: [640, 320, 320, 320, 320, 320, 320, 320],
          x: [0, 375, 750, 1125, 0, 375, 750, 1125],
          y: [0, 0, 0, 0, 320, 320, 320, 320],
          z: [0, 0, 0, 0, 0, 0, 0, 0],
          background: [
            "#1e9cd9",
            "#0277bd",
            "#2f5aa8",
            "#242424",
            "#0277bd",
            "#0277BD",
            "#242424",
            "#242424"
          ]
        },
        {
          speed: [525, 640, 640, 320, 375, 337, 375, 375],
          width: [375, 375, 375, 188, 375, 375, 375, 375],
          height: [640, 640, 640, 320, 320, 320, 320, 320],
          x: [0, 375, 750, 1125, 0, 375, 750, 1125],
          y: [0, 0, 0, 0, 320, 320, 320, 320],
          z: [0, 0, 0, 0, 0, 0, 0, 0],
          background: [
            "#1e9cd9",
            "#0277bd",
            "#2f5aa8",
            "#242424",
            "#0277bd",
            "#0277BD",
            "#242424",
            "#242424"
          ]
        },
        {
          speed: [525, 375, 375, 320, 320, 320, 320, 375],
          width: [1125, 375, 375, 375, 1125, 375, 375, 375],
          height: [320, 320, 320, 320, 320, 320, 320, 160],
          x: [0, 375, 750, 1125, 0, 375, 750, 1125],
          y: [0, 0, 0, 0, 320, 320, 320, 320],
          z: [0, 0, 0, 0, 0, 0, 0, 0],
          background: [
            "#1e9cd9",
            "#0277bd",
            "#2f5aa8",
            "#242424",
            "#0277bd",
            "#0277BD",
            "#242424",
            "#242424"
          ]
        },
        {
          speed: [525, 337, 640, 337, 337, 337, 337, 320],
          width: [750, 375, 375, 375, 375, 375, 375, 188],
          height: [640, 320, 640, 320, 320, 320, 320, 320],
          x: [0, 375, 750, 1125, 0, 375, 750, 1125],
          y: [0, 0, 0, 0, 320, 320, 320, 320],
          z: [0, 0, 0, 0, 0, 0, 0, 0],
          background: [
            "#1e9cd9",
            "#0277bd",
            "#2f5aa8",
            "#242424",
            "#0277bd",
            "#0277BD",
            "#242424",
            "#242424"
          ]
        }
      ],
      items: []
    };
  },
  created() {
    // 뷰가 생성되고 데이터가 이미 감시 되고 있을 때 데이터를 가져온다.
    // this.fetchData()
  },
  watch: {
    // 라우트가 변경되면 메소드를 다시 호출됩니다.
    // '$route': 'fetchData'
    /* locale (val) {
      this.$i18n.locale = val
    } */
  },
  mounted() {
    const visualArray = this.fn_array(this.$refs["visual"]);
    // let nameT = null
    // let nameF = null
    let idx = null;
    // console.log(visualArray)

    router.beforeEach((to, from, next) => {
      idx = to.meta.index;
      this.isTitle = to.meta.title;
      this.isTitleSub = to.meta.titleSub;
      this.isLoad = true;
      // console.log(to.path)
      /*
      const toDepth = to.path.split('/').length
      const fromDepth = from.path.split('/').length
      this.transitionName = toDepth < fromDepth ? 'slide-right' : 'slide-left'
      */
      /*next();
      return;*/

      if (to.path.split("/").length > 2) {
        this.transitionName = to.meta.transition;
        // console.log(to.meta.transition)
        next();
      } else {
        const tr = this.transitionName.split("-");
        this.transitionName = tr[0];
        if (tr[1] === "top") {
          this.transitionName = this.transitionName + "-" + "bottom";
        } else if (tr[1] === "right") {
          this.transitionName = this.transitionName + "-" + "left";
        } else if (tr[1] === "bottom") {
          this.transitionName = this.transitionName + "-" + "top";
        } else if (tr[1] === "left") {
          this.transitionName = this.transitionName + "-" + "right";
        } else if (tr[1] === "in") {
          this.transitionName = this.transitionName + "-" + "out";
        } else if (tr[1] === "out") {
          this.transitionName = this.transitionName + "-" + "in";
        } else {
          this.transitionName = "fade";
        }
        this.fn_sub(next, this.blocks[idx], visualArray, idx);
      }
    });

    router.afterEach((to, from) => {
      // console.log('afterEach')
      this.isLoad = false;
    });
  },
  computed: {},
  methods: {
    fetchData() {
      // 데이터 가져오기 위한 유틸리티/API 래퍼로 변경합니다.
    },
    fn_array(elements) {
      let arr = [];
      for (var i = 0; i < elements.childNodes.length - 1; i++) {
        arr[i] = elements.childNodes[i];
      }
      return arr;
    },
    fn_sub: function(next, blockSize, visual, idx) {
      // this.fn_size(visual, blockSize, 0, next);
      // const loop = this.fn_test();
      const vLangth = visual.length - 1;
      if (idx > 0) {
        visual.forEach(function(element, index) {
          // console.log(element,' / ',index,' / ',element.children)
          if (element.children.length > 0) {
              element.children[0].classList.add("hide");
          }
          // element.style.willChange = 'width, height, transform, opacity';
          Velocity(
            element,
            {
              //width: blockSize.width[index],
              //height: blockSize.height[index],
              "max-width": blockSize.width[index],
              "max-height": blockSize.height[index],
              left: blockSize.x[index],
              top: blockSize.y[index],
            },
            {
              duration: 300,
              // easing: [0.42, 0.0, 0.58, 1.0],
              easing: [0.25, 0.8, 0.5, 1],
              complete: function() {
                // element.style.willChange = 'auto';
                if (element.children.length > 0) {
                  element.children[0].classList.remove("hide");
                }
                //element.removeAttribute("style");
                if (index === vLangth || idx === 0) {
                  next();
                }
              }
            }
          );
        });
      } else {
        visual[0].children[0].classList.add('hide')
        Velocity(
          visual[0],
          {
            width: blockSize.width[0],
            height: blockSize.height[0],
            "max-width": blockSize.width[0],
            "max-height": blockSize.height[0]
          },
          {
            duration: 400,
            // easing: [0.25, 0.8, 0.5, 1],
            easing: [0.25, 0.8, 0.5, 1],
            complete: function() {
              visual[0].children[0].classList.remove('hide')
              visual[0].removeAttribute("style");
              next();
            }
          }
        );
      }
    }
  }
};
</script>

<style lang="scss">
