<template>
  <v-app v-bind:class="isDetail">
    <guide axisV></guide>
    <header>
      <v-navigation-drawer
        dark
        app
        disable-resize-watcher
        fixed
        :clipped="$vuetify.breakpoint.lgAndUp"
        v-model="sidebar"
        right
        flat
        overflow
      >
        <menu-bar menuPosition="side"></menu-bar>
      </v-navigation-drawer>
      <v-toolbar app flat :clipped-left="$vuetify.breakpoint.lgAndUp" height="64px">
        <v-toolbar-title>{{appName}}</v-toolbar-title>
        <v-spacer></v-spacer>

        <menu-bar
          v-bind:class="{ active: position }"
          class="hidden-sm-and-down"
          menuPosition="top"
          v-if="this.$route.meta.layout === 'default'"
        ></menu-bar>

        <v-btn fab round flat @click="fn_back" v-else>
          <v-icon class="clear">clear</v-icon>
        </v-btn>

        <v-toolbar-side-icon
          class="sidebar hidden-md-and-up"
          @click.stop="sidebar = !sidebar"
          v-if="this.$route.meta.layout === 'default'"
        ></v-toolbar-side-icon>
      </v-toolbar>
    </header>
    <!-- <v-content>
      <transition v-bind:name="transitionName" appear>
        <component :is="layout">
          <router-view></router-view>
        </component>
      </transition>
    </v-content>-->
    <v-content>
      <component :is="layout">
        <router-view></router-view>
      </component>
    </v-content>

    <footer-bar></footer-bar>
    <!-- <v-footer app></v-footer> -->
  </v-app>
</template>

<script>
import store from "./store";
import router from "./router";
import Guide from "@/components/Guide.vue";
import MenuBar from "@/components/Menus.vue";
import FooterBar from "@/components/Footer.vue";

const default_layout = "default";

export default {
  components: {
    Guide,
    MenuBar,
    FooterBar
  },
  computed: {
    layout() {
      return (this.$route.meta.layout || default_layout) + "-layout";
    }
  },
  data() {
    return {
      isMobile: false,
      isDetail: "default",
      sidebar: false,
      position: false,
      appName: "appName",
      activeClass: "cancel-transition",
      errorClass: "",
      transitionName: "zoom-in"
    };
  },
  beforeDestroy() {
    if (typeof window !== "undefined") {
      window.removeEventListener("resize", this.fn_resize, { passive: true });
    }
  },
  created() {
    this.appName = this.$store.state.appName;
  },
  mounted() {
    this.fn_resize();
    //this.fn_detail();
    window.addEventListener("resize", this.fn_resize, { passive: true });

    router.beforeEach((to, from, next) => {
      this.isDetail = to.meta.layout;

      if (to.meta.index > from.meta.index) {
        this.position = true;
      } else {
        this.position = false;
      }
      if (to.meta.transition) {
        this.transitionName = "zoom-in";
      } else {
        this.transitionName = "zoom-out";
      }
      //console.log("isLayout::", this.isLayout, "position:", this.layout);
      next();
    });
  },
  methods: {
    fn_detail() {
      console.log("fn_detail()");
      this.isDetail = this.$router.history.current.meta.layout;
    },
    fn_resize() {
      this.isMobile = window.innerWidth < 750;
    },
    fn_back: function() {
      //router.go(-1);
      history.back();
    }
  }
};
</script>

